*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: "Roboto", -apple-system, "Segoe UI", "Open Sans", "Helvetica Neue", sans-serif;
  margin: 0;
  color: white;
  background: linear-gradient(-135deg, #53b8bb, #f3f2c9);
  height: 100%;
  width: 100%;
  font-size: 1.3rem;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.container {
  max-width: 720px;
  margin: 3.125rem auto 0 auto;
}

#title {
  font-size: 2.3rem;
}

.header {
  padding: 0 0.625rem;
  margin-bottom: 1.875rem;
}

.header-title {
  text-align: center;
  font-size: 1.3rem;
  color: #003638;
}

p {
  font-size: 1.5rem;
}

h1,
p {
  margin-top: 0;
  margin-bottom: 0.5rem;
}

.description {
  font-style: italic;
  font-weight: 200;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.4);
}

form {
  background: #003638b0;
  padding: 2.5rem 2rem 1.5rem 2rem;
  border-radius: 0.5rem;
  box-shadow: 5px 5px 10px #003638;
}

.form-control {
  display: block;
  width: 100%;
  height: 3rem;
  padding: 0.5rem 0.5rem;
  color: #003638;
  background-color: white;
  background-clip: padding-box;
  border: 1px solid #055052;
  border-radius: 0.25rem;
  border-color: #003638;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.form-control:focus {
  border-color: #003638;
  outline: 0;
  box-shadow: 0 0 0 0.2rem #055052;
}

.form-group {
  margin: 0 auto 1.25rem auto;
  padding: 0.25rem;
}

label {
  display: flex;
  align-items: center;
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

input,
button,
select,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

.input-textarea {
  min-height: 120px;
  width: 100%;
  padding: 0.625rem;
  resize: none;
  color: #003638;
}

.input-radio,
.input-checkbox {
  display: inline-block;
  margin-right: 0.7rem;
  min-height: 1.25rem;
  min-width: 1.25rem;
  background-color: #003638;
}

.img-ico {
  padding-right: 10px;
}

.submit-button {
  display: block;
  width: 100%;
  padding: 0.75rem;
  background: #003638;
  color: inherit;
  border-radius: 5px;
  cursor: pointer;
}

.submit-button:hover {
  background: #055052;
}

button {
  border: none;
}

footer {
  color: #003638;
  font-size: 1rem;
  text-align: center;
  padding: 2rem;
}